import Ten from '../sound/ten.mp3';
import Five from '../sound/five.mp3';
import Four from '../sound/four.mp3';
import Three from '../sound/three.mp3';
import Two from '../sound/two.mp3';
import One from '../sound/one.mp3';
import Beep from '../sound/beepNext.mp3';

export default function playSound(seconds) {
  //if counter is 10 or five play sound (one more because of delay)
  if (seconds === 10) {
    let audio = new Audio(Ten);
    audio.play();
  }
  if (seconds === 5) {
    let audio = new Audio(Five);
    audio.play();
  }
  if (seconds === 4) {
    let audio = new Audio(Four);
    audio.play();
  }
  if (seconds === 3) {
    let audio = new Audio(Three);
    audio.play();
  }
  if (seconds === 2) {
    let audio = new Audio(Two);
    audio.play();
  }
  if (seconds === 1) {
    let audio = new Audio(One);
    audio.play();
  }
  if (seconds === 0) {
    let audio = new Audio(Beep);
    audio.play();
  }
}
